body {
  margin: 0;
  font-family:Arial, Helvetica, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
}

.form-container {
  background-color: whitesmoke;
  width: 50%;
  display: flex;
  flex-direction: column;
}

.updated-form-container {
  background-color: whitesmoke;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.checkbox-container {
  display: flex;
  justify-content: space-between;
  min-width: 100px;
  max-width: 200px;
}

h3 {
  margin: 15px 0 15px 30px;
}

.no-claims {
  margin: 10px 0 10px 30px;
}

h4 {
  margin: 0 0 5px 0;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 85%;
  margin: 0 auto 20px;
  background-color: white;
  border: 1px solid rgba(151, 151, 151, 0.412);
  border-radius: 8px;
  padding: 15px;
}

textarea {
  width: 100%;
  height: 100px;
  padding: 10px;
  margin-top: 8px;
  box-sizing: border-box;
  border: 2px #ccc;
  border-radius: 5px;
  background-color: whitesmoke;
  font-size: 16px;
  font-family: inherit;
  outline: none;
  resize: none;
}

p {
  margin: 8px 0;
  color: black;
  font-size: 18px;
}

.sub-heading {
  color: rgb(156, 156, 156);
}

label {
  font-size: 18px;
}

input {
  background-color: whitesmoke;
  outline: none;
  border: none;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 5px;
  font-size: 16px;
}

.upload-area {
  display: flex;
  max-width: 450px;
  justify-content: space-between;
}

.upload-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background: whitesmoke;
  color: red;
  width: 200px;
  padding: 5px;
  margin: 5px;
  height: 175px;
  border-radius: 8px;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.submit {
  background-color: red;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  margin-bottom: 20px;
}

.submit:hover {
  opacity: 75%;
  background: rgb(37, 37, 37);
  /* border: 1px solid black; */
}

button {
  cursor: pointer;
  
}

.main-claims-container {
  /* height: 150vh; */
  width: 50%;
  display: flex;
  flex-direction: column;
  /* justify-content: space-around; */
      
}

.claims-container {
  /* border: 1px solid grey; */
  border-radius: 8px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.view-claim-btn-container {
  display: flex;
  justify-content: center;
}

.claim {
  margin: 15px auto;
  border: 1px solid rgba(130, 130, 130, 0.412);
  border-radius: 8px;
  padding: 15px;
  width: 60%;
}

.modal {
  z-index: 5;
  position: fixed;
  display: flex;
  flex-direction: column;
  word-wrap: break-word;
  width: 500px;
  top: 50%;
  left: 50%;
  background: whitesmoke;
  border: 1px solid rgb(120, 120, 120);
  border-radius: 10px;
  transform: translate(-50%, -50%);
}

.claim-details {
  margin: 0 30px;
}

.update-modal {
  z-index: 5;
  position: fixed;
  overflow-y: auto;
  width: 500px;
  top: 50%;
  left: 50%;
  background: whitesmoke;
  width: 600px;
  height: 500px;
  border: 1px solid rgb(120, 120, 120);
  border-radius: 10px;
  transform: translate(-50%, -50%);
}

.map-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  background: whitesmoke;
  width: 70%;
  height: 70%;
  border: 1px solid rgb(120, 120, 120);
  border-radius: 10px;
  transform: translate(-50%, -50%);
  padding: 10px;
}

.map-btn {
  position: absolute;
  z-index: 10;
  background-color: red;
  color: white;
  border: 1px solid black;
  border-radius: 50%;
  height: 15px;
  width: 15px;
    
}

.marker {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: red;
  border: 1px solid black;
}

.btn-container {
  display: flex;
  justify-content: space-between;
  /* padding: 0 10px; */
  margin: 10px 30px 20px;
}

.claim-btn {
  background-color: whitesmoke;
  color: black;
  padding: 10px 15px;
  border-radius: 8px;
  border: 1px solid black;
}

.claim-btn:hover {
  background-color: red;
  color: white;
  border: 1px solid transparent;
}

.claim-details-btn {
  background-color: white;
  color: black;
  width: 125px;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid black;
}

.claim-details-btn:hover {
  background-color: red;
  color: white;
  border: 1px solid transparent;
}

.close-btn {
  background-color: red;
  color: white;
  border-radius: 50%;
  border: 1px solid black;
  margin: 10px;
  height: 15px;
  width: 15px;
}
a {
  font-size: 18px;
  font-family: inherit;
}


img {
  width: 50px;
  border-radius: 5px;
}

.file-input {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.upload-label {
  cursor: pointer;
  padding: 35% 50%;
}


.thumbnails-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  /* height: 100%; */
}

.thumbnail {
  padding: 5px;
  margin: 5px;
}

.claim-thumbnail {
  display: inline-block;
  margin-right: 10px;
}

.upload-photos-btn {
  background-color: white;
  color: black;
  padding: 10px 20px;
  border-radius: 8px;
  border: 1px solid black;
  margin-bottom: 20px;
}

.upload-photos-btn:hover {
  background-color: whitesmoke;
  border: 1px solid black;
}

input[type=checkbox] {
  accent-color: red;
}

.header {
  display: flex;
  align-items: center;
  margin: 10px 10px
}

.content {
  display: flex;
}

.shadow {
  box-shadow: 0px 0px 2px rgb(114, 114, 114);
}

.captain-logo {
  width: 100px;
  height: 33px;
}

.damage-checks {
  margin: 0;
  padding-top: 3px; 
}